import Navbar from "./components/Navbar";
import AboutPage from "./page/about/page";
import EducationPage from "./page/education/page";
import ExperiencePage from "./page/experience/page";
import IntroPage from "./page/intro/page";
import SkillsPage from "./page/skills/page";

export default function Home() {
  return (
    <>
      <IntroPage />
      <Navbar hidden="hidden" resumeBtn={"btn-outline"} />
      <div className="layout">
        <AboutPage />
        <EducationPage />
        <ExperiencePage />
        <SkillsPage />
      </div>
    </>
  );
}
